import React from 'react';

import './features.scss';
import featImg1 from '../../assets/jpg/features-1.jpg';
import featImg2 from '../../assets/jpg/features-2.jpg';
import featImg3 from '../../assets/jpg/features-3.jpg';

const Features = () => {
    return (
        <>
            <div className="background-gray">
                <div className="wrapper features-wrapper">
                    <h2 id="features">
                        Features
                    </h2>
                    <div className="features-slides">
                        <div className="features-slide">
                            <img src={featImg1} alt=""/>
                            <div className="features-slide-typography">
                                <h3>
                                    Supports All Currencies and Cards
                                </h3>
                                <p>
                                    We support all popular currencies and is fully customizable to add
                                </p>
                                <a href="#" className="more-btn">Read More</a>
                            </div>
                        </div>
                        <div className="features-slide">
                            <img src={featImg2} alt=""/>
                            <div className="features-slide-typography">
                                <h3>
                                    Manage your expenses on the go
                                </h3>
                                <p>
                                    You can access your account from anywhere in the world on any device
                                </p>
                                <a href="#" className="more-btn">Read More</a>
                            </div>
                        </div>
                        <div className="features-slide">
                            <img src={featImg3} alt=""/>
                            <div className="features-slide-typography">
                                <h3>
                                    Cloud Budget Management Software
                                </h3>
                                <p>
                                    Our software is made so you can access and manage your budget
                                </p>
                                <a href="#" className="more-btn">Read More</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Features;