import React from 'react';

import './focused.scss';
import focusedImg from '../../assets/svg/focused.svg';
import logoPlayStore from '../../assets/svg/focused-logo-play.svg';
import logoAppStore from '../../assets/svg/focused-logo-appstr.svg';
import logoWindows from '../../assets/svg/focused-logo-windws.svg';

const Focused = () => {
    return (
        <>
            <div className="focused-backgr">
                <div className="wrapper focused-wrapper">
                    <div className="focused-typography">
                        <h2>
                            Stay focused on saving money
                        </h2>
                        <p>
                            It is important to stay focused on saving money
                            in any way you can. We help you monitor your
                            spending habits so you can easily spot and cut
                            any unnecessary expenses. Simply join today to get started!
                        </p>
                        <a href="#" className="more-btn">Learn more</a>
                        <div className="focused-logos">
                            <img src={logoPlayStore} alt="play store"/>
                            <img src={logoAppStore} alt="logo appstore"/>
                            <img src={logoWindows} alt="logo windows"/>
                        </div>
                    </div>
                    <div className="img-container">
                        <img src={focusedImg} alt="stay focused"/>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Focused;