import React from 'react';
import {useState} from "react";

import './header.scss';
import logo from '../../assets/svg/header-logo.svg';
import burgerMenu from '../../assets/svg/burger-menu.svg';

const Header = () => {

    const body = document.querySelector('body');

    const [activeMenu, toggleActiveMenu] = useState(true);

    function toggleMenuMode() {
        if (window.screen.width <= 500) {
            toggleActiveMenu(!activeMenu);
            body.classList.toggle('fix');
        }
    }

    return (
        <>
            <header>
                <div className="wrapper header-wrapper">
                    <div className="logo-container">
                        <a href="">
                            <img src={logo} alt=""/>
                        </a>
                    </div>
                    <div className="navbar">
                        <ul className={activeMenu ? "menu-list" : "menu-list-hide"}>
                            <li><a href="#overview" onClick={toggleMenuMode}>Overview</a></li>
                            <li><a href="#features" onClick={toggleMenuMode}>Features</a></li>
                            <li><a href="#" onClick={toggleMenuMode}>Technology</a></li>
                            <li><a href="#contact" onClick={toggleMenuMode}>Contact</a></li>
                            <li><a href="#" onClick={toggleMenuMode}>Sign up</a></li>
                        </ul>
                    </div>
                    <a href="#" className="login-button">
                        Log in
                    </a>
                    <div className="burger-container" onClick={toggleMenuMode}>
                        <img src={burgerMenu} alt=""/>
                    </div>
                </div>
                <div className="wrapper">
                    <div className="hero-wrapper">
                        <h1>CloudBudget</h1>
                        <p>Cloud budget management for everyone. Only 69.95
                            a Month After a 7 Day Trial of
                            <br/>
                            Up to 4.99</p>
                        <a href="#" className="hero-button">
                            Sign up
                        </a>
                    </div>
                </div>

            </header>
        </>
    )
}

export default Header;