import React from 'react';

import './cloudManagement.scss';
import videoSection from '../../assets/jpg/video-section.jpg';

const CloudManagement = () => {
    return (
        <>
            <div className="wrapper management-wrapper">
                <div className="management-typography">
                    <h2 id="overview">Easy to Use Cloud Budget
                        <br/>
                        Management Software</h2>
                    <p>
                        Our software is made so you can access and manage your
                        budget and expenses online at any time from any device.
                        It provides detailed income and expense reports with graphs
                        so you can easily see your spending patterns and budget
                        at a glance. Read below to find out more.
                    </p>
                    <a href="#" className="more-btn">Learn more</a>
                </div>
                <div className="img-container">
                    <img src={videoSection} alt="video section"/>
                </div>
            </div>
        </>
    )
}

export default CloudManagement;